@media only screen and (max-width: 768px){
  .App{
  // ONLY MOBILES
    align-items: center;
    display: flex;
    justify-content: center;
    padding: 30px 40px;
    text-align: center;
    animation-name: fade;
    animation-duration: 3000ms;
    // height: 100vh;
    padding: 30px 40px;
    // height: 100vh;
  }

  .Caption{
    font-weight: lighter;
    font-size: small;
    font-style: italic;
  }


  $randomNumber: random(3);

  .Floater{
    animation-name: floating;
    animation-duration: 5s;
    // animation-duration: $randomNumber + s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
  }


  @keyframes floating{
    0% { transform: translate(0, 0px);}
    50% { transform: translate(0, 15px);}
    100% { transform: translate(0, -0px);}
  }

  .GoalArray{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  h1{
    font-weight: lighter;
    font-size: 64px;
  }

  .Icon{
    width: 5vh;
  }

  .Loader{
    animation-iteration-count: 1;
    height: 100vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
  }

  .navIcons{
    align-items: center;
    display: flex;
    justify-content: space-evenly;
    flex: 1;
  }

  @keyframes fade {
    0% {
      opacity: 0;
    }
  
    25% {
      opacity: 1;
    }
  
    75% {
      opacity: 1;
    }
  
    100% {
      opacity: 0;
    }
  }



  .Task{
    background-color: #e7e7e7;
    border-radius: 15px;
    box-shadow: 13px 16px 29px rgba(63, 63, 63, 0.2);
    display: none;
    margin: 15px 40px;
    padding: 1px 0px ;
    text-align: center;
  }

  .Task_Mobile{
    display: block;
  }

  .Step{
    // background-color: #f5f5f5;
    // border-radius: 15px;
    // box-shadow: 13px 16px 29px rgba(63, 63, 63, 0.2);
    // margin: 15px 40px;
    // padding: 1px 0px ;
    font-weight: normal;
    text-align: center;
  }

  .Title{
    background-color: beige;
    border-radius: 15px;
    margin: 15px 40px;
    padding: 15px;
    text-align: center;
  }

  .toDoItem{
    align-items: center;
    background: #F5F5F5;
    border-radius: 12px;
    box-shadow: 13px 16px 29px rgba(0, 0, 0, 0.15);
    display: flex;
    height: 49px;
    justify-content: space-around;
    margin: 11px;
    // padding: 20px;
    width: 236px;
  }

  .thirtyThree > .colorBar{
    background: #EBBD87;
    border-radius: 7px;
    height: 38px;
    // margin: 30px;
    width: 7px;
  }

  .thirtyThree > li{
    color: #59370F;
    font-family: SF Pro Display;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    list-style: none;

  }


  .toDoItem > .checkButton{
    background: #FFFFFF;
    border: 2px solid #EBBD87;
    border-radius: 100%;
    box-sizing: border-box;
    width: 23px;
    height: 23px;
  }

  .thirtyThree{
    align-items: center;
    // border: 2px solid purple;
    display: flex;
    flex-direction: row;
    flex: 0.33;
  }

  .thirtyThree > * {
    margin: 0px 12px;
  }
}
